<template>
  <div class="card_container">
    <el-card>
      <el-tabs type="border-card">
        <div class="workers__header">
          <div class="worker_info"></div>
          <el-row style="margin: 0px auto;width:80vh;display: flex;">
            <el-col :span="6">
              <el-input v-model="filterWorker" placeholder="" clearable @input="handleSearch"></el-input>
            </el-col>
            <el-button @click="handleSearch" type="primary" round style="width: 10%; margin-left: 10px;"></el-button>
          </el-row>
           <span class="worker_info_add"> 
             <el-popover
              placement="top"
              v-model="addVisible1">
                 <el-button type="primary" slot="reference"></el-button>
                <el-button size="mini" type="text" @click="addMore"></el-button>
                <el-button type="primary" size="mini" @click="addWorker"></el-button>
            </el-popover>
             
            </span>
        </div>
        
        <el-table v-loading="loading"
          :data="paginatedData"
           border
          :header-cell-style="{ background: 'rgb(248,249,250)', color: 'rgb(85,85,85)' }"
           height="650"
          style="width: 100% ;overflow-y: auto; ">
          <el-table-column
            fixed
            prop="num"
            label=""
            width="100">
            <template #default="scope">
              {{ (currentPage - 1) * pageSize + scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
            width="120">
          </el-table-column>
          <el-table-column
            prop="dutyText"
            label=""
            width="140">
          </el-table-column>
          <el-table-column
            prop="phone"
            label=""
            width="150">
          </el-table-column>
          <el-table-column
            prop="workAreaShow"
            label=""
            min-width="300">
          </el-table-column>
          <el-table-column
            fixed="right"
            label=""
            width="350">
            <template slot-scope="scope">
              <el-button @click="edit(scope.row)" type="primary" size="middle"></el-button>
              <el-button @click="resetPassword(scope.row)" type="primary" size="middle"></el-button>
              <el-button @click="remove(scope.row.id)" type="danger" size="middle"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 25, 50, 100, 200]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="filteredData.length"
        ></el-pagination>

        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="35%">
          <el-form label-width="100px" :model="dialogForm">
            <el-form-item label="" prop="name" required>
              <el-input v-model="dialogForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone" required>
              <el-input v-model="dialogForm.phone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="duty" required>
              <el-select v-model="dialogForm.duty" placeholder="" @change="handleDutyChange">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item 
              label="" 
              prop="workArea" 
              v-if="dialogForm.duty === '1'"
              required>
              <div class="area-tags-container">
                <el-tag
                  v-for="tag in dynamicTags"
                  :key="tag.key"
                  closable
                  @close="handleClose(tag)">
                  {{ tag.display }}
                </el-tag>
                <el-cascader 
                  v-if="inputVisible"
                  v-model="selectedArea"
                  :options="address_options"
                  :props="{
                    label: 'area',
                    value: 'key',
                    emitPath: false,
                  }"
                  @change="handleInputConfirm"
                  placeholder=""
                  clearable
                  class="cascader-input">
                </el-cascader>
                <el-button 
                  v-else 
                  class="button-new-tag" 
                  size="small" 
                  @click="showInput">
                  + 
                </el-button>
                <div class="area-tips" >
                  {{ dynamicTags.length }}
                </div>
                <!-- <div class="area-tips" v-else>
                  
                </div> -->
              </div>
            </el-form-item>
            <el-form-item 
              label="" 
              v-if="dialogForm.duty === '2' || dialogForm.duty === '3'">
              <div class="special-worker-tips">
                {{ dialogForm.duty === '2' ? '' : '' }}
              </div>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="submitDialog">{{ dialogType === 'add' ? ' ' : ' ' }}</el-button>
          </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="batchImportVisible" width="60%">
          <div class="batch-import-header" style="display: flex;"> 
            <el-button type="text" @click="downloadTemplate">Excel</el-button> &nbsp;&nbsp;
            <img src="@/assets/.png" style="width: 40%; height: 3.5vw;" />
          </div>
          <div class="batch-import-container">
            <!--  -->
            <div class="upload-section">
              <el-upload
                class="upload-demo"
                drag
                action=""
                :auto-upload="false"
                :on-change="handleFileChange"
                :show-file-list="false"
                accept=".xlsx, .xls"
              >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">Excel<em></em></div>
                <div class="el-upload__tip" slot="tip">.xlsx.xls</div>
              </el-upload>
              
              <div v-if="file" class="file-info">
                <i class="el-icon-document"></i>
                <span>{{ file.name }}</span>
                <el-button type="text" @click="removeFile"></el-button>
              </div>
            </div>
            
            <!--  -->
            <div class="preview-section" v-if="importedData.length > 0">
              <div class="section-header">
                <h3></h3>
                <el-button type="primary" @click="submitBatchImport"></el-button>
              </div>
              
              <el-table :data="importedData" border height="400" style="width: 100%">
                <el-table-column prop="name" label="" width="120">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.name" placeholder=""></el-input>
                  </template>
                </el-table-column>
                <el-table-column prop="phone" label="" width="150">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.phone" placeholder=""></el-input>
                  </template>
                </el-table-column>
                <el-table-column prop="duty" label="" width="150">
                  <template slot-scope="scope">
                    <el-select v-model="scope.row.duty" placeholder="">
                      <el-option label="" value="0"></el-option>
                      <el-option label="" value="1"></el-option>
                      <el-option label="" value="2"></el-option>
                      <el-option label="" value="3"></el-option>
                    </el-select>
                  </template>
                </el-table-column>
                <el-table-column prop="workArea" label="" min-width="250">
                  <template slot-scope="scope">
                    <div v-if="scope.row.duty === '1'">
                      {{ scope.row.workArea }}
                    </div>
                    <div v-else class="no-area-tip">
                      {{ getDutyText(scope.row.duty) }}
                    </div>
                  </template>
                </el-table-column>
                <el-table-column label="" width="100">
                  <template slot-scope="scope">
                    <el-button type="danger" icon="el-icon-delete" circle @click="removeImportRow(scope.$index)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
              
              <div class="import-stats">
                <span> {{ importedData.length }} </span>
                <el-button type="text" @click="addEmptyRow">+ </el-button>
              </div>
            </div>
          </div>
          
          <div slot="footer" class="dialog-footer">
            <el-button @click="batchImportVisible = false"> </el-button>
            <el-button type="primary" @click="submitBatchImport" :disabled="importedData.length === 0"></el-button>
          </div>
        </el-dialog>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';

export default {
  data() {
    return {
      filterWorker: '',
      pageSize: 10,
      currentPage: 1,
      dialogVisible: false,
      dialogType: 'add', // 'add'  'edit'
      dialogForm: {
        id: '',
        name: '',
        phone: '',
        duty: '1',
        work_area: ''
      },
      tableData: [],
      tableDataCopy: [],
      filteredData: [],
      dynamicTags: [],
      inputVisible: false,
      selectedArea: null,
      address_options: [],
      addVisible1: false,
      batchImportVisible: false,
      file: null,
      importedData: [],
      keyToDisplayMap: null,
      loading: true
    };
  },

  computed: {
    // 
    paginatedData() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredData.slice(start, end);
    },
    
    // 
    dialogTitle() {
      return this.dialogType === 'add' ? '' : '';
    }
  },

  mounted() {
    this.fetchData();
  },
  
  methods: {
    // 
    fetchData() {
      this.loading = true;
      Promise.all([
        this.$axios.get('/area/getAllArea2'),
        this.$axios.get('/user/getAllWorkersDutyNot0')//
      ]).then(([areaRes, workerRes]) => {
        this.address_options = areaRes.data;
        this.buildAreaMaps();
        this.processWorkerData(workerRes.data);
        this.loading = false;
      }).catch(error => {
        console.error(':', error);
        this.loading = false;
        this.$message.error('');
      });
    },
    
    // 
    processWorkerData(workerData) {
      this.tableData = workerData.map(worker => {
        // 
        worker.dutyText = this.getDutyText(worker.duty);
        
        // 
        if (worker.work_area && worker.duty === '1') {
          const keys = worker.work_area.split(',');
          const displayNames = keys.map(key => this.getDisplayName(key));
          worker.workAreaShow = displayNames.join('; ');
        } else {
          if (worker.duty == '1') {
            worker.workAreaShow = null;
          } else {
            worker.workAreaShow = worker.duty === '0' ? '' : '';
          }
          
        }
        return worker;
      });
      this.tableDataCopy = [...this.tableData];
      this.filteredData = [...this.tableData];
    },
    
    // 
    getDutyText(duty) {
      switch(duty) {
        case '0': return '';
        case '1': return '';
        case '2': return '';
        case '3': return '';
        default: return '';
      }
    },
    
    // 
    handleSearch() {
      if (!this.filterWorker.trim()) {
        this.filteredData = [...this.tableDataCopy];
        this.currentPage = 1;
        return;
      }
      const searchTerm = this.filterWorker.toLowerCase().trim();
      this.filteredData = this.tableDataCopy.filter(worker => 
        worker.name.toLowerCase().includes(searchTerm) || 
        worker.phone.includes(searchTerm)
      );
      this.currentPage = 1;
    },
    
    // 
    handleSizeChange(size) {
      this.pageSize = size;
      this.currentPage = 1;
    },
    
    handleCurrentChange(page) {
      this.currentPage = page;
    },
    
    // 
    addWorker() {
      this.addVisible1 = false;
      this.dialogType = 'add';
      this.dialogForm = {
        id: '',
        name: '',
        phone: '',
        duty: '1',
        work_area: ''
      };
      this.dynamicTags = [];
      this.dialogVisible = true;
    },
    
    // 
    edit(row) {
      this.dialogType = 'edit';
      this.dialogForm = {
        id: row.id,
        name: row.name,
        phone: row.phone,
        duty: row.duty.toString(),
        work_area: row.work_area || ''
      };
      
      // 
      if (row.duty === '1' && row.work_area) {
        this.initializeTags(row.work_area);
      } else {
        this.dynamicTags = [];
      }
      
      this.dialogVisible = true;
    },
    
    // 
    submitDialog() {
      if (!this.dialogForm.name || !this.dialogForm.phone) {
        this.$message.warning("");
        return;
      }
      
      // // 
      // if (this.dialogForm.duty === '1' && this.dynamicTags.length === 0) {
      //   this.$message.warning("");
      //   return;
      // }
      
      // 
      if (this.dialogForm.duty === '1') {
        this.dialogForm.work_area = this.dynamicTags.map(tag => tag.key).join(',');
      } else {
        this.dialogForm.work_area = ''; // 
      }
      console.log(this.dialogForm);
      
      const url = this.dialogType === 'add' ? '/user/addWorker' : '/user/updateUser';
      this.$axios.post(url, this.dialogForm).then(res => {
        if (res.data.code === 200) {
          this.$message.success(this.dialogType === 'add' ? '' : '');
          this.dialogVisible = false;
          this.fetchData();
        } else {
          this.$message.error(res.data.message || '');
        }
      }).catch(() => {
        this.$message.error('');
      });
    },
    
    // 
    resetPassword(row) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(() => {
        const id = row.id;
        const phone = row.phone;
        this.$axios.post(`/user/upPassword?id=${id}&phone=${phone}`).then(res => {
          if (res.data.code === 200) {
            this.$message.success('');
          } else {
            this.$message.error('');
          }
        });
      }).catch(() => {
        this.$message.info("");
      });
    },
    
    // 
    remove(id) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(() => {
        this.$axios.post(`/user/removeUser?id=${id}`).then(res => {
          if (res.data.code === 200) {
            this.$message.success('');
            this.fetchData();
          } else {
            this.$message.error('');
          }
        });
      }).catch(() => {});
    },
    
    // 
    handleDutyChange(value) {
      // 
      if (value !== '1') {
        this.dynamicTags = [];
      }
    },
    
    // ---  ---
    // 
    initializeTags(workAreaStr) {
      this.dynamicTags = [];
      if (!workAreaStr) return;
      
      const keys = workAreaStr.split(',').filter(key => key.trim() !== '');
      keys.forEach(key => {
        const displayName = this.getDisplayName(key);
        this.dynamicTags.push({ key, display: displayName });
      });
    },
    
    // 
    buildAreaMaps() {
      this.keyToDisplayMap = new Map();
      
      const processNode = (node, parent = null) => {
        if (parent) {
          //  + 
          const display = parent.area + node.area;
          this.keyToDisplayMap.set(node.key, display);
        } else {
          // 
          this.keyToDisplayMap.set(node.key, node.area);
        }
        
        // 
        if (node.children) {
          node.children.forEach(child => processNode(child, node));
        }
      };
      
      this.address_options.forEach(area => processNode(area));
    },
    
    // key
    getDisplayName(key) {
      if (this.keyToDisplayMap?.get(key)) {
        return this.keyToDisplayMap.get(key);
      }
      
      // 
      let result = `(${key})`;
      if (this.address_options.length > 0) {
        for (const parent of this.address_options) {
          if (parent.key === key) {
            return parent.area;
          }
          if (parent.children) {
            for (const child of parent.children) {
              if (child.key === key) {
                return parent.area + child.area;
              }
            }
          }
        }
      }
      return result;
    },
    
    // 
    showInput() {
      // if (this.dynamicTags.length >= 5) {
      //   this.$message.warning('5');
      //   return;
      // }
      this.inputVisible = true;
      this.selectedArea = null;
    },
    
    // 
    handleInputConfirm() {
      if (this.selectedArea) {
        const key = this.selectedArea;
        const display = this.getDisplayName(key);
        
        // 
        if (!this.dynamicTags.some(tag => tag.key === key)) {
          this.dynamicTags.push({ key, display });
        } else {
          this.$message.warning('');
        }
      }
      this.inputVisible = false;
    },
    
    // 
    handleClose(tag) {
      this.dynamicTags = this.dynamicTags.filter(t => t.key !== tag.key);
    },
    
    // ---  ---
    // 
    addMore() {
      this.batchImportVisible = true;
      this.file = null;
      this.importedData = [];
    },
    
    downloadTemplate() {
      // 
      const headers = ['', '', '(0-,1-,2-,3-)', '()'];
      
      // 
      const examples = [
        ['', '13800138000', '1', '1,2'],
        ['', '13900139000', '2', ''],
        ['', '13700137000', '3', '']
      ];
      
      // 
      const wb = XLSX.utils.book_new();
      
      // 
      const wsData = [headers, ...examples];
      
      // 
      const ws = XLSX.utils.aoa_to_sheet(wsData);
      
      // 
      const colWidths = [15, 15, 30, 40];
      ws['!cols'] = colWidths.map(width => ({ width }));
      
      // 
      XLSX.utils.book_append_sheet(wb, ws, '');
      
      // Excel
      const wbout = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
      
      // Blob
      const blob = new Blob([wbout], { type: 'application/octet-stream' });
      saveAs(blob, '.xlsx');
    },
    
    // 
    handleFileChange(file, fileList) {
      this.file = file.raw;
      this.readExcelFile(file.raw);
    },
    
    // 
    removeFile() {
      this.file = null;
      this.importedData = [];
    },
    
    // Excel
    readExcelFile(file) {
      const reader = new FileReader();
      
      reader.onload = (e) => {
        try {
          const data = new Uint8Array(e.target.result);
          const workbook = XLSX.read(data, { type: 'array' });
          
          // 
          const worksheet = workbook.Sheets[workbook.SheetNames[0]];
          
          // JSON
          const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
          
          // 
          const filteredData = jsonData.filter((row, index) => {
            return index > 0 && row.length > 0 && row.some(cell => cell !== null && cell !== '');
          });
          
          // 
          this.processImportedData(filteredData);
        } catch (error) {
          console.error('Excel:', error);
          this.$message.error('Excel');
        }
      };
      
      reader.readAsArrayBuffer(file);
    },
    
    // 
    processImportedData(data) {
      if (!data || data.length === 0) {
        this.$message.warning('Excel');
        return;
      }
      
      this.importedData = data.map(row => {
        // 
        while (row.length < 4) row.push('');
        
        const [name, phone, duty, workArea] = row;
        
        // 
        let dutyValue = '1'; // 
        if (duty !== undefined && duty !== null) {
          if (['0', '1', '2', '3'].includes(duty.toString())) {
            dutyValue = duty.toString();
          } else if (typeof duty === 'string') {
            // 
            if (duty.includes('')) dutyValue = '0';
            else if (duty.includes('')) dutyValue = '2';
            else if (duty.includes('')) dutyValue = '3';
          }
        }
        
        return {
          name: name || '',
          phone: phone ? phone.toString() : '',
          duty: dutyValue,
          workArea: workArea || '',
          workAreaValue: dutyValue === '1' ? this.parseWorkArea(workArea || '') : []
        };
      });
      
      this.$message.success(` ${this.importedData.length} `);
    },
    
    // 
    parseWorkArea(areaStr) {
      if (!areaStr) return [];
      
      // 
      const areaParts = areaStr.split(/[,;\s]+/).filter(part => part.trim());
      
      // key
      const matchedKeys = [];
      
      // 
      for (const part of areaParts) {
        const found = this.findAreaKey(part.trim());
        if (found) {
          matchedKeys.push(found);
        } else {
          console.warn(`: ${part}`);
        }
      }
      
      return matchedKeys;
    },

    // key
    findAreaKey(areaName) {
      // 
      for (const yuan of this.address_options) {
        if (yuan.area === areaName) {
          return yuan.key;
        }
        
        if (yuan.children) {
          for (const dong of yuan.children) {
            if (dong.area === areaName) {
              return dong.key;
            }
          }
        }
      }
      
      return null;
    },
    
    // 
    addEmptyRow() {
      this.importedData.push({
        name: '',
        phone: '',
        duty: '1',
        workArea: '',
        workAreaValue: []
      });
    },
    
    // 
    removeImportRow(index) {
      this.importedData.splice(index, 1);
    },
    
    // 
    submitBatchImport() {
      // 
      const invalidRows = this.importedData.filter(row => 
        !row.name || !row.phone || !row.duty
      );
      
      if (invalidRows.length > 0) {
        this.$message.error(` ${invalidRows.length} `);
        return;
      }
      
      // 
      const submitData = this.importedData.map(row => ({
        name: row.name,
        phone: row.phone,
        duty: row.duty,
        work_area: row.duty === '1' ? row.workAreaValue.join(',') : ''
      }));
      
      // API
      this.$axios.post('/user/batchAddWorkers', submitData).then(res => {
        if (res.data.code === 200) {
          this.$message.success(` ${res.data.data || submitData.length} `);
          this.batchImportVisible = false;
          this.fetchData();
        } else {
          this.$message.error(res.data.message || '');
          if (res.data.code === 400) {
            this.$notify({
              title: '',
              message: res.data.data,
              duration: 0
            });
          }
        }
      }).catch(error => {
        console.error(':', error);
        this.$message.error('');
      });
    }
  },
};
</script>

<style scoped>
.card_container {
  padding: 20px;
}

.worker_info {
  font-size: large;
  margin-bottom: 10px;
  border-left: 7px solid rgb(228, 70, 49);
  padding-left: 10px;
  font-weight: bold;
}

.workers__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  gap: 15px;
}

.worker_info_add {
  flex-shrink: 0;
}

.el-tag {
  margin-right: 10px;
  margin-bottom: 10px;
}

.button-new-tag {
  margin-top: 10px;
}

.cascader-input {
  width: 100%;
  margin-top: 10px;
}

.el-pagination {
  margin-top: 20px;
  text-align: center;
  padding: 15px 0;
}

.dialog-footer {
  text-align: center;
}

.area-tags-container {
  border: 1px solid #ebeef5;
  border-radius: 4px;
  padding: 10px;
  background-color: #fafafa;
}

.area-tips {
  font-size: 12px;
  color: #909399;
  margin-top: 8px;
}

.special-worker-tips {
  padding: 10px;
  background-color: #f0f9ff;
  border-radius: 4px;
  border-left: 4px solid #1890ff;
  color: #606266;
}

.no-area-tip {
  color: #909399;
